@import url(https://fonts.googleapis.com/css?family=PT+Serif:400,400i,700);
/*

color theme 

cyan: 
  #006064
  Light - #428e92
  Dark - #00363a

Blue Grey: 
  #263238
  Light - #4f5b62
  Dark - #000a12

*/
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

#NavBar {
  height: 50px;
  width: 100vw;
  background: #000a12;
  margin-top: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

#NavBar > h3 {
  color: #428e92;
  padding: 0.5rem;
}

#NavBar > ul {
  color: #428e92;
  font-family: "PT Serif", "serif";
  font-weight: 700;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  height: 50px;
  margin-left: auto;
  margin-right: 15px;
}

#NavBar > ul > li {
  border-radius: 20px;
  padding: 0.5rem;
}
#NavBar > ul > li:hover {
  color: white;
  background: #428e92;
  box-shadow: black 1px 1px 10px;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}

h1 {
  color: #006064;
}

.App {
  text-align: center;
  font-family: 'PT Serif', 'serif';
}

.modal-backdrop {
  background: rgba(0,0,0,0.5);
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.modal {
  background: white;
  height: 300px;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 100px;
  width: 400px;
}

